body {
  background-color: #fdfbe8;
  transition: background-color 1s ease;
}
.hero {
  height: 100vh;
  .hero-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 0%;

    header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 20px 5%;
      padding-bottom: 0;
      .logo-container {
        z-index: 6;
      }
      .navbar {
        .nav-menu {
          margin-top: 0;
          display: flex;
          align-items: flex-start;
          justify-content: center;
          flex-direction: column;
          gap: 48px;
          list-style: none;
          position: fixed;
          top: 0;
          opacity: 0;
          padding-inline: 0;
          left: 100%;
          width: 100%;
          height: 100vh;
          background-color: #fdfbe8;
          z-index: 5;
          padding: 80px 20px;
          box-sizing: border-box;
          transition: opacity 1s ease-in-out;
          li {
            width: 50%;
            a {
              text-decoration: none;
              font-family: Dela Gothic One;
              font-size: 32px;
              font-weight: 400;
              line-height: 41.6px;
              text-align: left;
              color: #1663b1;
            }
          }
          li:hover {
            a {
              color: #c0ddeb;
            }
          }
        }
        .nav-menu.active {
          opacity: 1;
          left: 0;
        }
      }
    }
    .hero__content {
      display: flex;
      flex-direction: column-reverse;
      justify-content: space-evenly;
      padding-inline: 5%;
      height: 100%;
      .content__text {
        display: flex;
        flex-direction: column;
        h1 {
          color: #1663b1;
          font-family: Dela Gothic One;
          font-size: 28px;
          font-weight: 400;
          text-align: left;
          line-height: 36.4px;
          text-transform: uppercase;
        }
        p {
          font-family: Fixel Display;
          font-size: 18px;
          font-weight: 400;
          text-align: left;
          color: #353535;
          font-size: clamp(20px, 1.5vw + 1rem, 22px);
        }

        .btn {
          position: relative;
          border-radius: 20px;
          background-color: #1663b1;
          width: 100%;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          padding: 20px 32px;
          box-sizing: border-box;
          text-align: left;
          font-size: 20px;
          color: #8affff;
          font-family: "Dela Gothic One";
          .lets-do-it {
            position: relative;
            line-height: 120%;
          }
        }
      }
      .content__image {
        width: 100%;
        aspect-ratio: 16/9;
        align-self: center;
        position: relative;

        svg {
          .st0 {
          }
        }
        img {
          width: 150px;
          height: fit-content;
          position: absolute;
          right: 7%;
          top: 5%;
        }
      }
    }
  }
}

.sparkling {
  padding-inline: 5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;

  .sparkling-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: space-evenly;

    .aboutWaves {
      align-self: flex-start;
    }
    .sparkling-h2 {
      text-align: left;
      color: #1663b1;
      text-transform: uppercase;
      font-family: Dela Gothic One;
      font-size: 24px;
      font-weight: 400;
      line-height: 31.2px;
      letter-spacing: 0.01em;
    }
    .sparkling-text {
      font-family: Fixel Display;
      font-size: 20px;
      font-weight: 400;
      line-height: 26px;
      text-align: left;
      color: #1663b1;
      strong {
        font-family: Fixel Display;
        font-size: 20px;
        font-weight: 800;
        line-height: 26px;
        text-align: left;
        color: #1663b1;
      }
    }
    .btn {
      width: 50%;
      position: relative;
      border-radius: 20px;
      background-color: #fdfbe8;
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;

      box-sizing: border-box;
      font-size: 20px;
      color: #010202;
      font-family: "Dela Gothic One";
      .lets-do-it {
        display: flex;
        align-items: center;
        gap: 10px;
        color: #1663b1;
        padding: 20px 32px;
      }
    }
  }
}

.specialise {
  display: flex;
  flex-direction: column;
  align-items: center;
  h2 {
    font-family: Dela Gothic One;
    font-size: 26px;
    font-weight: 400;
    line-height: 33.8px;
    text-align: left;
    color: #c0ddeb;
  }
  .specialise__text {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    margin-inline: 5%;
    color: #cfccb2;
    gap: 13%;
    .typography {
      height: 100vh;
      h3 {
        font-family: Dela Gothic One;
        font-size: 28px;
        font-weight: 400;
        line-height: 36.4px;
        text-align: left;
        text-transform: uppercase;
      }
      p {
        font-family: Fixel Display;
        font-size: 16px;
        font-weight: 400;
        line-height: 19.36px;
        text-align: left;
      }
      .sliderTypography {
        display: flex;
        gap: 10px;
        overflow-x: auto;
        scroll-snap-type: x mandatory;
        -webkit-overflow-scrolling: touch; /* Для поддержки плавной прокрутки на iOS */
      }
      .slideT {
        flex: 0 0 auto;
        width: 100%;
        scroll-snap-align: start;
        img {
          width: 100%;
          height: auto;
        }
      }
    }
    .text__web {
      color: #fdfbe8;
    }
    .text__graphic {
      color: #3e5e8e;
    }
  }
}

.development {
  height: 100vh;
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-around;
  padding-inline: 5%;

  .psevdolinks {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    justify-content: center;
    .link {
      color: #1663b1;
      text-decoration: none;
      padding: 10px 20px;
      border: 1px solid #1663b1;
      border-radius: 40px;
      width: fit-content;
      transition: opacity 0.3s ease;
    }
    .link:hover {
      border: 1px solid #8affff;
    }
    .link:hover ~ .link {
      opacity: 0.5; /* Затемняем остальные ссылки */
    }
  }
  .development__content {
    h2 {
      color: #1663b1;
      font-family: Dela Gothic One;
      font-size: 32px;
      font-weight: 400;
      line-height: 41.6px;
      text-align: left;
      text-transform: uppercase;
    }
    .content__text {
      display: flex;
      flex-direction: column;
      gap: 7%;
      font-family: Fixel Display;
      font-size: 24px;
      font-weight: 500;
      line-height: 31.2px;
      text-align: left;
      color: #1663b1;
    }
  }
}

//contact

.contact {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  h2 {
    width: 1005;
    margin-inline: 5%;
    font-family: Dela Gothic One;
    font-size: 28px;
    font-weight: 400;
    line-height: 28px;
    text-align: Center;
    color: #fdfbe8;
    margin-bottom: 0;
    text-transform: uppercase;
  }
  .contact__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 50px;

    div {
      text-align: center;
      display: flex;
      flex-direction: column;
      gap: 9px;
      h4 {
        color: #c0ddeb;
        font-family: Fixel Display;
        font-size: 22px;
        font-weight: 600;
        text-align: center;
        text-transform: capitalize;
        margin-block: 0;
      }
      a {
        font-family: Fixel Display;
        font-size: 20px;
        font-weight: 400;
        color: #fdfbe8;
        text-decoration: none;
      }
    }
    div:hover {
      cursor: pointer;
      position: relative;
      h4 {
        color: #8affff;
      }
    }
  }

  footer {
    width: 100%;
    display: flex;
    gap: 28px;
    margin-top: 5%;
    
    justify-content: center;
  }
}

$popup-bg-color: #fdfbe8;
$content-bg-color: #fdfbe8;
$close-color: #aaa;
$close-hover-color: black;
$form-bg-color: #fdfbe8;
$input-border-color: #ccc;
$button-bg-color: #1663b1;
$button-hover-bg-color: #005f6b;
$padding: 10%;

/* Popup background */

.popup {
  display: none;
  position: fixed;

  z-index: 7;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow: auto;
  background-color: $popup-bg-color;
  opacity: 0;
  transition: opacity 1s ease-in-out;

  &-content {
    background-color: $content-bg-color;
    padding: $padding;
    width: 100%;
    max-width: 100%;
    height: 100vh;
    box-sizing: border-box; /* Ensure padding is included in width */
    text-align: center;
    overflow-x: hidden; /* Prevent horizontal overflow */

    h2 {
      font-family: Dela Gothic One;
      font-size: 32px;
      font-weight: 400;
      line-height: 41.6px;
      text-align: left;
      color: #1663b1;
      box-sizing: border-box; /* Ensure padding is included in width */
    }

    .close {
      color: $close-color;
      float: right;
      font-size: 28px;
      font-weight: bold;

      &:hover,
      &:focus {
        color: #1663b1;
        text-decoration: none;
        cursor: pointer;
      }
    }

    form {
      margin-top: 10%;
      display: flex;
      flex-direction: column;
      gap: 60px;
      background-color: $form-bg-color;
      height: 100%;
      max-width: 100%;
      box-sizing: border-box; /* Ensure padding is included in width */
      overflow-x: hidden; /* Prevent horizontal overflow */

      .formLine {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap; /* Allow inputs to wrap to avoid overflow */

        input {
          background-color: $form-bg-color;
          font-family: Fixel Display;
          font-size: 24px;
          font-weight: 400;
          line-height: 29.04px;
          text-align: left;
          color: #2b3e62;
          width: 45%; /* Adjust width to avoid overflow */
          box-sizing: border-box; /* Ensure padding is included in width */
          border-bottom: 1px solid $input-border-color;

          ::placeholder {
            opacity: 1;
          }
        }
      }

      textarea {
        padding: 10px;
        margin-bottom: 15px;
        border-bottom: 1px solid $input-border-color;
        border-radius: 4px;
        font-family: Fixel Display;
        font-size: 24px;
        font-weight: 400;
        line-height: 29.04px;
        text-align: left;
        color: #2b3e62;
        resize: none;
        width: 100%; /* Ensure textarea does not exceed 100% of the form width */
        box-sizing: border-box; /* Ensure padding is included in width */

        &:focus {
          outline: none;
        }

        &::-webkit-scrollbar {
          width: 10px;
        }
        &::-webkit-scrollbar-track {
          background: #f1f1f1;
        }
        &::-webkit-scrollbar-thumb {
          background: #1663b1;
          border-radius: 5px;
        }
        &::-webkit-scrollbar-thumb:hover {
          background: #555;
        }
      }

      button {
        cursor: pointer;
        position: relative;
        border-radius: 20px;
        background-color: #1663b1;
        width: 180px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        padding: 20px 32px;
        box-sizing: border-box; /* Ensure padding is included in width */
        text-align: left;
        font-size: 20px;
        color: #8affff;
        font-family: "Dela Gothic One";

        .lets-do-it {
          position: relative;
          line-height: 120%;
        }
      }
    }
  }
}

.show {
  display: block;
  opacity: 1;
}
