.hero{
    
    height: 100vh;
    .hero-container{
        height: 100vh;
        display: flex;
        flex-direction: column;
        gap: 0%;

        header{
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 20px 5%;
            padding-bottom: 0;
            .logo-container{
                z-index: 6;
            }
            .navbar{
                .nav-menu{
                    margin-top: 0;
                    display: flex;
                    align-items: flex-start;
                    justify-content: center;
                    flex-direction: column;
                    gap: 48px;
                    list-style: none;
                    position: fixed;
                    top: 0;
                    opacity: 0;
                    padding-inline: 0;
                    left: 100%;
                    width: 100%;
                    height: 100vh;
                    background-color: #FDFBE8;
                    z-index: 5;
                    padding: 80px 20px;
                    box-sizing: border-box;
                    transition: opacity 1s ease-in-out;
                    li{
                        width: 50%;
                        a{
                            text-decoration: none;
                            font-family: Dela Gothic One;
                            font-size: 32px;
                            font-weight: 400;
                            line-height: 41.6px;
                            text-align: left;
                            color: #1663B1;
                        }
                        
                    }
                    li:hover{
                        a{
                            color: #C0DDEB;
                            
                        }
                    }
 

                   
                }
                .nav-menu.active {
                    opacity: 1;
                    left: 0;
                   
                  }
            }

        }
        .hero__content{
            display: flex;
            flex-direction: column-reverse;
            justify-content: space-evenly;
            padding-inline: 5%;
            height: 100%;
            .content__text{
                display: flex;
                flex-direction: column;
                margin-top: -10%;
                h1{
                    color: #1663B1;
                    font-family: Dela Gothic One;
                    font-size: 28px;
                    font-weight: 400;
                    text-align: left;
                    line-height: 36.4px;
                }
                p {
                    font-family: Fixel Display;
                    font-size: clamp(20px, 1.5vw + 1rem, 22px);
                    font-weight: 400;
                    text-align: left;

                }
                
                 .btn {
                        position: relative;
                        border-radius: 20px;
                        background-color: #1663b1;
                        width: 100%;
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: center;
                        padding: 20px 32px;
                        box-sizing: border-box;
                        text-align: left;
                        font-size: 20px;
                        color: #8affff;
                        font-family: 'Dela Gothic One';
                        .lets-do-it {
                            position: relative;
                            line-height: 120%;
                            }
                        }
                    
            }
            .content__image{
                width: 100%;
                aspect-ratio: 16/9;
                align-self: center;
                position: relative;
                
 
                svg{
                   
                    .st0{
            
                    }
                }
                img{
                    width: 150px;
                    height: fit-content;
                    position: absolute;
                    right: 7%;
                    top: 5%;
                }
            }
        }
    }
}




