  $popup-bg-color: #FDFBE8;
$content-bg-color: #FDFBE8;
$close-color: #aaa;
$close-hover-color: black;
$form-bg-color: #FDFBE8;
$input-border-color: #ccc;
$button-bg-color: #1663B1;
$button-hover-bg-color: #005f6b;
$padding: 10%;

/* Popup background */

.no-scroll {
    overflow: hidden;
  }
.popup {
  display: none;
  position: fixed;
  z-index: 7;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow: auto;
  background-color: $popup-bg-color;
  opacity: 0;
  transition: opacity 1s ease-in-out;

  
  
  &-content {
    background-color: $content-bg-color;
    padding: $padding;
    width: 100%;
    height: 100vh;
    text-align: center;
    h2{
        font-family: Dela Gothic One;
        font-size: 32px;
        font-weight: 400;
        line-height: 41.6px;
        text-align: left;
        color: #1663B1 ;

    }
    .close {
      color: $close-color;
      float: right;
      font-size: 28px;
      font-weight: bold;
      
      &:hover,
      &:focus {
        color: #1663B1;
        text-decoration: none;
        cursor: pointer;
      }
    }
    
    form {
        margin-top: 10%;
      display: flex;
      flex-direction: column;
      gap: 60px;
      background-color: $form-bg-color;
      height: 100%;
      .formLine{
        display: flex;
        justify-content: space-between;
        input{
            background-color:  $form-bg-color;
            font-family: Fixel Display;
            font-size: 24px;
            font-weight: 400;
            line-height: 29.04px;
            text-align: left;
            color: #2B3E62;
            width: 40%;
            border-bottom: 1px solid $input-border-color;
            ::placeholder{

                opacity: 1;
            }
        }
      }
      
      
       textarea {
        padding: 10px;
        margin-bottom: 15px;
        border-bottom: 1px solid $input-border-color;
        border-radius: 4px;
        font-family: Fixel Display;
        font-size: 24px;
        font-weight: 400;
        line-height: 29.04px;
        text-align: left;
        color: #2B3E62;
        resize: none;
        &:focus {
        
            outline: none; 
      }
        &::-webkit-scrollbar {
            width: 10px;
          }/* Remove default outline */
          &::-webkit-scrollbar-track {
            background: #f1f1f1; 
          }
          &::-webkit-scrollbar-thumb {
            background: #1663b1; 
            border-radius: 5px;
          }
          &::-webkit-scrollbar-thumb:hover {
            background: #555; 
          }
      }

      
      button {
        cursor: pointer;
        position: relative;
        border-radius: 20px;
        background-color: #1663b1;
        width: 180px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        padding: 20px 32px;
        box-sizing: border-box;
        text-align: left;
        font-size: 20px;
        color: #8affff;
        font-family: 'Dela Gothic One';
        .lets-do-it {
            position: relative;
            line-height: 120%;
            }
        }
    }
  }
}
.show {
    display: block;
    opacity: 1;
} 